.goods__wrapper {
  width: 280px;
  margin: 0 auto;
}

.goods__title {
  @include fontStyle(36px, 36px);
  font-weight: 400;
  margin: 0;
  padding-top: 27px;
  padding-bottom: 41px;
}

.goods__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 19px;
}
